.about-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 50px;
    max-width: 1200px;
    margin: auto;
    color: #ffffff;
    gap: 40px;
    transition: background 1s ease-in-out;
  }
  
  /* Level 1 - Default UI */
  .about-container {
    background: linear-gradient(135deg, #111, #222);
  }
  
  /* Level 2 - Green Neon Theme */
  .about-container.level-2 {
    background: linear-gradient(135deg, #003300, #006600);
  }
  
  /* Level 3 - Futuristic Dark Mode */
  .about-container.level-3 {
    background: linear-gradient(135deg, #1b1b1b, #000);
    animation: pulse 2s infinite alternate;
  }
  
  @keyframes pulse {
    0% { box-shadow: 0 0 10px #00ff00; }
    100% { box-shadow: 0 0 20px #00ff00; }
  }
  
  /* Left Side - Text */
  .about-text {
    flex: 1;
    text-align: left;
  }
  
  .about-text h2 {
    font-size: 2.5rem;
    color: #00ff00;
    text-shadow: 0 0 10px #00ff00;
    margin-bottom: 20px;
  }
  
  .about-text p {
    font-size: 1.2rem;
    line-height: 1.6;
    color: #ccc;
  }
  
  /* XP Progress Bar */
  .xp-bar-container {
    margin-top: 20px;
  }
  
  .xp-bar {
    background-color: #333;
    height: 15px;
    width: 100%;
    border-radius: 10px;
    overflow: hidden;
    margin: 5px 0;
  }
  
  .xp-fill {
    height: 100%;
    background-color: #00ff00;
    transition: width 0.5s ease-in-out;
  }
  
  /* Gain XP Button */
  .gain-xp-btn {
    margin-top: 15px;
    margin-left: 50%;
    padding: 10px 20px;
    background-color: #00ff00;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    font-size: 1rem;
    transition: transform 0.2s, background-color 0.2s;
  }
  
  .gain-xp-btn:hover {
    background-color: #00cc00;
  }
  
  .gain-xp-btn:active {
    transform: scale(0.95);
  }
  
  /* Right Side - Avatar */
  .about-avatar {
    flex: 1;
    text-align: center;
  }
  
  .about-avatar img {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    border: 3px solid #00ff00;
    box-shadow: 0 0 15px #00ff00;
    transition: transform 0.5s ease-in-out;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .about-container {
      flex-direction: column;
      text-align: center;
    }
  
    .about-text {
      text-align: center;
    }
  
    .about-avatar {
      margin-top: 20px;
    }
  }

  /* Auction Puzzle Input & Button */
.puzzle-input {
    margin-top: 10px;
    padding: 8px;
    font-size: 1rem;
    border: 2px solid #00ff00;
    background: #222;
    color: white;
    border-radius: 5px;
    width: 200px;
    text-align: center;
  }
  
  .puzzle-btn {
    margin-left: 10px;
    padding: 8px 15px;
    background-color: #00ff00;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: 0.2s;
  }
  
  .puzzle-btn:hover {
    background-color: #00cc00;
  }
  
  .puzzle-success {
    color: #00ff00;
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 10px;
  }
  
  .puzzle-fail {
    color: red;
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 10px;
  }
  
  /* Level 3 UI Enhancements */
  .about-container.level-3 {
    background: linear-gradient(135deg, #1b1b1b, #000);
    animation: pulse 2s infinite alternate;
  }
  
  @keyframes pulse {
    0% { box-shadow: 0 0 10px #00ff00; }
    100% { box-shadow: 0 0 20px #00ff00; }
  }
  
  