.projects-container {
  padding: 50px;
  text-align: center;
}

.projects-container h1 {
  font-size: 2.5rem;
  color: #00ff00;
  margin-bottom: 30px;
}

/* Category Buttons */
.category-buttons {
  margin-bottom: 20px;
}

.category-btn {
  padding: 10px 15px;
  margin: 5px;
  background-color: #222;
  border: 2px solid #00ff00;
  color: #00ff00;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: 0.3s;
}

.category-btn.active {
  background-color: #00ff00;
  color: black;
}

.category-btn:hover {
  background-color: #00cc00;
}

/* XP Progress Bar */
.xp-bar-container {
  margin-top: 20px;
}

.xp-bar {
  background-color: #333;
  height: 15px;
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
  margin: 5px 0;
}

.xp-fill {
  height: 100%;
  background-color: #00ff00;
  transition: width 0.5s ease-in-out;
}

/* Project Grid */
.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Responsive grid */
  gap: 10px;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  margin: auto;
}

.project-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background: #1e1e1e;
  color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  min-height: 350px; /* ✅ Ensuring a uniform card height */
}

.project-image {
  width: 100%;
  max-height: 200px; /* ✅ Restrict image height */
  object-fit: cover;
  border-radius: 8px;
}


.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px #00ff00;
}

.project-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 5px;
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: #222;
  padding: 20px;
  border-radius: 10px;
  max-width: 500px;
  width: 90%;
  text-align: left;
}

.modal-content h2 {
  color: #00ff00;
}
