.contact-container {
    text-align: center;
    padding: 40px;
  }
  
  .xp-bar-container {
    margin: 20px auto;
    width: 80%;
    max-width: 500px;
  }
  
  .xp-bar {
    height: 10px;
    background: #333;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
  }
  
  .xp-fill {
    height: 100%;
    background: linear-gradient(90deg, #ff00ff, #ffcc00);
    transition: width 0.5s ease-in-out;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: auto;
    gap: 10px;
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .contact-form button {
    padding: 10px;
    background: #ffcc00;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
  }

  .social-media {
    text-align: center;
    margin-top: 20px;
  }
  
  .social-icons {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 10px;
  }
  
  .social-icon {
    font-size: 2rem;
    transition: transform 0.2s ease-in-out;
  }
  
  .social-icon:hover {
    transform: scale(1.2);
  }
  
  .linkedin {
    color: #0077b5;
  }
  
  .github {
    color: #333;
  }
  
  .instagram {
    color: #e4405f;
  }
  
  